import { FC } from 'react';

import { Nav, NavText, PrevButton, NextButton } from '../Carousel.styles';
import { SimpleControlsProps } from '../Carousel.types';

const Controls: FC<SimpleControlsProps> = ({
  handlePrev,
  handleNext,
  itemCount,
  currentIndex,
  contentType = 'Image',
}) => {
  const isAtStart = currentIndex === 0;
  const isAtEnd = currentIndex === itemCount - 1;

  return (
    <Nav aria-label="slide controls">
      <NavText>{`${contentType} ${currentIndex + 1} of ${itemCount}`}</NavText>
      <PrevButton
        aria-label="Previous"
        isDisabled={isAtStart}
        onClick={handlePrev}
        data-testid="prev-button"
      />
      <NextButton
        aria-label="Next"
        isDisabled={isAtEnd}
        onClick={handleNext}
        data-testid="next-button"
      />
    </Nav>
  );
};

export default Controls;
