import { useRef, useState, useEffect, useMemo } from 'react';

interface UseCaptionHeightProps {
  itemCount: number;
}

interface UseCaptionHeighReturnValue {
  handleCaptionRef: (index: number) => (element: HTMLDivElement | null) => void;
  captionHeight: number;
}

const useCaptionHeight = ({
  itemCount,
}: UseCaptionHeightProps): UseCaptionHeighReturnValue => {
  const initialRefs = Array.from({ length: itemCount }, () =>
    useRef<HTMLDivElement | null>(null),
  );
  const captionRefs = useMemo(() => initialRefs, [initialRefs]);
  const [captionHeight, setCaptionHeight] = useState(0);

  useEffect(() => {
    const resizeObserver = new ResizeObserver((entries) => {
      let tallestCaption: number = 0;

      entries.map((entry) => {
        // get the height of the caption text
        const currentHeight = entry.target.children[0].clientHeight;

        if (currentHeight > tallestCaption) {
          tallestCaption = currentHeight;
        }
      });

      setCaptionHeight(tallestCaption);
    });

    captionRefs.forEach((ref) => {
      const caption = ref.current;

      if (caption) {
        resizeObserver.observe(caption);
      }
    });

    return () => resizeObserver.disconnect();
  }, [captionRefs]);

  const handleCaptionRef =
    (index: number) => (element: HTMLDivElement | null) => {
      captionRefs[index].current = element;
    };

  return {
    handleCaptionRef,
    captionHeight,
  };
};

export default useCaptionHeight;
