import { styled, css } from 'styled-components';

import {
  MediaPlusCaptionForSlider,
  Caption,
} from '#components/MediaPlusCaption';

import * as device from '#theme/devices';

import Carousel from '../Carousel';
import { List, Nav, NavText } from '../Carousel.styles';

interface SimpleCarouselStylesProps {
  $captionHeight: number;
}

export const StyledCarousel = styled(Carousel)<SimpleCarouselStylesProps>`
  ${({ $captionHeight }) => css`
    --caption-height: ${$captionHeight ? `${$captionHeight}px` : 'auto'};
  `}
  --gap: ${({ theme }) => theme.spacing.x0_5}px;
  --padding: ${({ theme }) => theme.spacing.x1_5}px;
  --nav-min-height: ${({ theme }) =>
    theme.dimension.minTouchArea}px; /** min height for controls on tablets */
  --nav-width: 205px; /** set width for controls on tablets */
  gap: var(--gap);
  padding: var(--padding);
  background: ${({ theme }) => theme.color.canvas.secondary};

  ${NavText} {
    margin-right: ${({ theme }) => theme.spacing.x2}px;
    font: ${({ theme }) => theme.textStyle.media.pagination};
  }

  @media ${device.onlyTablet} {
    --padding: ${({ theme }) => theme.spacing.x4}px
      ${({ theme }) => theme.spacing.x4}px ${({ theme }) => theme.spacing.x2}px;
    position: relative;

    ${Nav} {
      position: absolute;
      right: 0;
      bottom: 0;
      width: var(--nav-width);
      height: var(--caption-height);
      min-height: var(--nav-min-height);
      padding-right: ${({ theme }) => theme.spacing.x4}px;
      padding-bottom: ${({ theme }) => theme.spacing.x2}px;
      background: ${({ theme }) => theme.color.canvas.secondary};
    }
  }

  @media ${device.laptop} {
    --gap: ${({ theme }) => theme.spacing.x1}px;
    --padding: ${({ theme }) => theme.spacing.x1_5}px
      ${({ theme }) => theme.spacing.x8}px ${({ theme }) => theme.spacing.x5}px;

    ${List} {
      order: 2;
    }

    ${Nav} {
      order: 1;
    }
  }
`;

export const StyledMediaPlusCaption = styled(MediaPlusCaptionForSlider)`
  ${Caption} {
    margin-top: ${({ theme }) => theme.spacing.x0_5}px;

    @media ${device.tablet} {
      margin-top: ${({ theme }) => theme.spacing.x1}px;
    }

    @media ${device.onlyTablet} {
      display: flex;
      width: calc(100% - (var(--nav-width)));
      height: var(--caption-height);
      min-height: var(--nav-min-height);
      align-items: center;
      padding-right: ${({ theme }) => theme.spacing.x2}px;
    }

    @media ${device.laptop} {
      line-height: 16px;
    }
  }
`;
