import { forwardRef } from 'react';

import { List, ListItem } from '../Carousel.styles';
import { SimpleSliderProps, Item } from '../Carousel.types';

const Slider = forwardRef<HTMLUListElement, SimpleSliderProps>(function Slider(
  { items, renderSlide, currentIndex, handleCaptionRef },
  ref,
) {
  const currentSlide = items[currentIndex];

  return (
    <List ref={ref}>
      {items.map((item: Item<unknown>, index: number) => {
        const isVisible = currentSlide === item;
        const captionRef = handleCaptionRef(index);

        return (
          <ListItem key={`item-${index}`} aria-hidden={!isVisible}>
            {renderSlide({ item, captionRef })}
          </ListItem>
        );
      })}
    </List>
  );
});

export default Slider;
