import { FC } from 'react';

import { SimpleCarouselProps } from '../Carousel.types';
import useCaptionHeight from '../hooks/useCaptionHeight';
import useSimpleSlider from '../hooks/useSimpleSlider';

import Controls from './Controls';
import { StyledCarousel } from './SimpleCarousel.styles';
import Slider from './Slider';

const SimpleCarousel: FC<SimpleCarouselProps> = ({
  title,
  items,
  renderSlide,
}) => {
  const itemCount = items.length || 0;
  const { sliderRef, currentIndex, handlePrev, handleNext } = useSimpleSlider();
  const { handleCaptionRef, captionHeight } = useCaptionHeight({ itemCount });

  if (itemCount === 0) {
    return null;
  }

  return (
    <StyledCarousel
      title={title}
      showTitle={false}
      description="Carousel with one slide shown at a time. Use the Previous and Next buttons to navigate between slides."
      $captionHeight={captionHeight}
    >
      <Slider
        items={items}
        renderSlide={renderSlide}
        currentIndex={currentIndex}
        handleCaptionRef={handleCaptionRef}
        ref={sliderRef}
      />
      <Controls
        handlePrev={handlePrev}
        handleNext={handleNext}
        itemCount={itemCount}
        currentIndex={currentIndex}
      />
    </StyledCarousel>
  );
};

export default SimpleCarousel;
