import {
  useEffect,
  useRef,
  useState,
  useCallback,
  MutableRefObject,
} from 'react';

interface UseSimpleSliderProps {
  initialIndex?: number;
}

interface UseSimpleSliderReturnValue {
  sliderRef: MutableRefObject<HTMLUListElement | null>;
  currentIndex: number;
  handlePrev: () => void;
  handleNext: () => void;
}

const useSimpleSlider = ({
  initialIndex = 0,
}: UseSimpleSliderProps = {}): UseSimpleSliderReturnValue => {
  const sliderRef = useRef<HTMLUListElement | null>(null);
  const [currentIndex, setCurrentIndex] = useState(initialIndex);
  const slider = sliderRef.current;

  useEffect(() => {
    if (!slider) return;

    const slides = Array.from(slider.children);

    const handleIntersection = (entries: IntersectionObserverEntry[]) => {
      entries.map((entry) => {
        if (entry.isIntersecting) {
          const scrollLeft = slider.scrollLeft;
          const slideWidth = slider.clientWidth;
          const newIndex = Math.round(scrollLeft / slideWidth);
          setCurrentIndex(newIndex);
        }
      });
    };

    const observer = new IntersectionObserver(handleIntersection, {
      root: slider,
      threshold: 0.5,
    });

    slides.forEach((slide) => {
      observer.observe(slide);
    });

    return () => observer.disconnect();
  }, [slider]);

  const useControls = useCallback((newIndex: number) => {
    const slider = sliderRef.current;

    if (!slider) return;

    const slideWidth = slider.clientWidth;

    slider.scrollTo({
      left: newIndex * slideWidth,
      behavior: 'smooth',
    });

    setCurrentIndex(newIndex);
  }, []);

  const handlePrev = () => useControls(currentIndex - 1);

  const handleNext = () => useControls(currentIndex + 1);

  return {
    sliderRef,
    currentIndex,
    handlePrev,
    handleNext,
  };
};

export default useSimpleSlider;
