import { FC } from 'react';

import {
  Header as VisibleHeader,
  ScreenReaderOnlyHeader,
  Title,
} from './Carousel.styles';
import { HeaderProps } from './Carousel.types';

const Header: FC<HeaderProps> = ({ title, showTitle }) => {
  const StyledHeader = showTitle ? VisibleHeader : ScreenReaderOnlyHeader;

  return (
    <StyledHeader>
      <Title>{title}</Title>
    </StyledHeader>
  );
};

export default Header;
